import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider, StorageService } from '@tei/core';
import { Observable } from 'rxjs';
import { IDeviceService } from '../@intermediate/device.interface';
import { GetDeviceRequest, GetDeviceResponse } from '../@intermediate/model/get-device.model';

@Injectable({
  providedIn: 'root'
})
export class DeviceService implements IDeviceService
{
  private readonly serviceUrl: string;
  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient,
    private storageService: StorageService)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/DeviceIntegration/Device';
  }
  Setting(request: GetDeviceRequest): Observable<GetDeviceResponse>
  {
    const apiUrl = this.serviceUrl + '/Setting';
    return this.http.get<GetDeviceResponse>(apiUrl, { params: request as any });
  }
}
