import { Injectable } from '@angular/core';
import { StorageService } from "@tei/core";
import { SignalrMessagingClientService } from '@tei/core-signalr';
import { DeviceInfoEnum } from "../@intermediate/model/get-device.model";


@Injectable()
export class MessagingClientService
{
  constructor(
    private signalrMessagingClientService: SignalrMessagingClientService,
    private storageService: StorageService,
  ) { }
  initMessagingClientService(): void
  {
    this.signalrMessagingClientService.init((this.storageService.Get(DeviceInfoEnum.DeviceInfoKey) as any).DeviceIdentifier, 'Bearer ' + (this.storageService.Get(DeviceInfoEnum.DeviceTokenInfoKey) as any).access_token);
  }
  disconnectMessagingClientService(): void
  {
    this.signalrMessagingClientService.disconnect();
  }
}
