import { Component, Inject, OnDestroy, OnInit } from '@angular/core';
import { DeviceSettingService } from '@tei/common-devices';
import { ManageLoadingService, StorageService } from '@tei/core';
import { SignalrMessagingClientService } from '@tei/core-signalr';
import { Subscription } from 'rxjs';
import { finalize } from 'rxjs/operators';
import { IDeviceService } from '../../@intermediate/device.interface';
import { CFDDeviceMessageModel, RequestType } from '../../@intermediate/model/cfd-device-message.model';
import { ComponentTypeEnm, ValueTypeEnm } from '../../@intermediate/model/device-custom-component.model';
import { DefaultTheme, DeviceInfoEnum, DeviceType, GetDeviceRequest, GetDeviceResponse, Setting } from '../../@intermediate/model/get-device.model';

@Component({
  selector: 'tei-cfd-container',
  templateUrl: './cfd-container.component.html',
  styleUrls: ['./cfd-container.component.scss']
})
export class CFDContainerComponent implements OnInit, OnDestroy
{
  orderStaticData: Setting;
  subscriptions: Subscription = new Subscription();
  isLight: boolean = false;
  componentTypeEnm = ComponentTypeEnm;
  valueTypeEnm = ValueTypeEnm;
  requestType = RequestType;
  cfdDataMessage: CFDDeviceMessageModel = null;
  defaultTheme = DefaultTheme;
  isShowSettings = true;

  constructor(
    @Inject('IDeviceService') private deviceService: IDeviceService,
    private storageService: StorageService,
    private signalRMessagingClientService: SignalrMessagingClientService,
    public manageLoadingService: ManageLoadingService,
    private deviceSettingService: DeviceSettingService)
  {
  }

  ngOnInit(): void
  {
    this.getOrdersStaticData();
  }

  signalRMessageReceived(): void
  {
    this.deviceSettingService.isShow.next(this.isShowSettings);
    this.subscriptions.add(
      this.signalRMessagingClientService.messageReceived.subscribe(
        {
          next: (message) =>
          {
            const messageObject: CFDDeviceMessageModel = JSON.parse(message.SerializedMessage);
            if (messageObject?.DeviceIdentifier !== this.orderStaticData?.DeviceSetupUID || message?.MessageType !== 'CFDDeviceMessaging')
            {
              return;
            }

            this.cfdDataMessage = messageObject;
            this.isShowSettings = (this.cfdDataMessage?.RequestType === this.requestType.CancelOrder || !this.cfdDataMessage);
            this.deviceSettingService.isShow.next(this.isShowSettings);
          },
          error: (err) =>
          {
            console.log(err);
          },
        })
    );
  };

  getOrdersStaticData(): void
  {
    const request: GetDeviceRequest = {
      DeviceSetupUID: (this.storageService.Get(DeviceInfoEnum.DeviceInfoKey) as any)?.DeviceIdentifier,
      DeviceType: DeviceType.CustomerFacingDisplay
    };
    this.manageLoadingService.startLoading();
    this.deviceService.Setting(request).pipe(finalize(() => this.manageLoadingService.finishLoading())).subscribe({
      next: (response: GetDeviceResponse) =>
      {
        this.orderStaticData = response.Setting;
        this.storageService.Set('profile-settings', { theme: response.Setting.DefaultTheme, language: response.Setting.CurrentCulture });
        this.setCurrentTheme(response.Setting.DefaultTheme);
        this.signalRMessageReceived();
      }
    });
  }

  setCurrentTheme(currentTheme: string): void
  {
    const body = document.body;
    body.classList.remove(body.classList.value.split(' ').filter(item => item.includes('nb-theme'))[0]);
    if (this.defaultTheme.Dark === currentTheme)
    {
      body.classList.add(`nb-theme-material-dark`);
    } else
    {
      body.classList.add(`nb-theme-material-light`);
    }
  }

  ngOnDestroy(): void
  {
    this.subscriptions.unsubscribe();
  }
}
