import { AfterViewInit, ChangeDetectorRef, ComponentFactoryResolver, Directive, Input, ViewContainerRef } from '@angular/core';
import { ComponentTypeEnm, DeviceCustomComponent } from '../../../@intermediate/model/device-custom-component.model';
import { CFDContainerBodyComponent } from '../dynamic-components/cfd-container-body/cfd-container-body.component';
import { CFDContainerFooterComponent } from '../dynamic-components/cfd-container-footer/cfd-container-footer.component';
import { CFDContainerHeaderComponent } from '../dynamic-components/cfd-container-header/cfd-container-header.component';

@Directive({
  selector: '[cfd-dynamic-container-component-generator]'
})
export class CFDDynamicContainerComponentGeneratorDirective implements AfterViewInit
{

  @Input() data: DeviceCustomComponent;
  componentTypeEnm = ComponentTypeEnm;

  constructor(public viewContainerRef: ViewContainerRef, private changeDetectorRef: ChangeDetectorRef,
    public componentFactoryResolver: ComponentFactoryResolver) { }

  ngAfterViewInit(): void
  {
    this.loadComponent();
    this.changeDetectorRef.detectChanges();
  };

  loadComponent(): void
  {
    this.viewContainerRef.clear();
    let component;

    switch (this.data.type)
    {
      case this.componentTypeEnm.Body: {
        component = CFDContainerBodyComponent;
        break;
      }
      case this.componentTypeEnm.Header: {
        component = CFDContainerHeaderComponent;
        break;
      }
      case this.componentTypeEnm.Footer: {
        component = CFDContainerFooterComponent;
        break;
      }
      default: {
        return;
      }
    }
    const componentFactory = this.componentFactoryResolver.resolveComponentFactory<any>(component);
    const componentRef = this.viewContainerRef.createComponent(componentFactory);
    componentRef.instance.data = this.data;
  }

}
