import { AfterViewInit, ChangeDetectorRef, ComponentFactoryResolver, Directive, Input, ViewContainerRef } from '@angular/core';
import { ComponentTypeEnm, DeviceCustomComponent } from '../../../@intermediate/model/device-custom-component.model';
import { CFDElementDuplexItemComponent } from '../dynamic-components/cfd-element-duplex-item/cfd-element-duplex-item.component';
import { CFDElementSubTitleComponent } from '../dynamic-components/cfd-element-sub-title/cfd-element-sub-title.component';
import { CFDElementTitleComponent } from '../dynamic-components/cfd-element-title/cfd-element-title.component';
import { CFDElementTriplexItemComponent } from '../dynamic-components/cfd-element-triplex-item/cfd-element-triplex-item.component';

@Directive({
  selector: '[cfd-dynamic-element-component-generator]'
})
export class CFDDynamicElementComponentGeneratorDirective implements AfterViewInit
{
  @Input() data: DeviceCustomComponent;
  @Input() index: number;
  componentTypeEnm = ComponentTypeEnm;

  constructor(public viewContainerRef: ViewContainerRef, private changeDetectorRef: ChangeDetectorRef,
    public componentFactoryResolver: ComponentFactoryResolver) { }

  ngAfterViewInit(): void
  {
    this.loadComponent();
    this.changeDetectorRef.detectChanges();
  };

  loadComponent(): void
  {
    this.viewContainerRef.clear();
    let component;

    switch (this.data.type)
    {
      case this.componentTypeEnm.DuplexItem: {
        component = CFDElementDuplexItemComponent;
        break;
      }
      case this.componentTypeEnm.TriplexItem: {
        component = CFDElementTriplexItemComponent;
        break;
      }
      case this.componentTypeEnm.Title: {
        component = CFDElementTitleComponent;
        break;
      }
      case this.componentTypeEnm.SubTitle: {
        component = CFDElementSubTitleComponent;
        break;
      }
      default: {
        return;
      }
    }
    const componentFactory = this.componentFactoryResolver.resolveComponentFactory<any>(component);
    const componentRef = this.viewContainerRef.createComponent(componentFactory);
    componentRef.instance.data = this.data;
    componentRef.instance.index = this.index;
  }
}
