import { Component, OnDestroy, OnInit } from '@angular/core';
import { MessagingClientService } from "./@services/messaging-client.service";
import { Router } from '@angular/router';

@Component({
  selector: 'tei-pos-cfd',
  templateUrl: './cfd.component.html',
})
export class CFDComponent implements OnInit, OnDestroy
{
  constructor(private messagingClientService: MessagingClientService, private router: Router)
  {
  }

  ngOnInit(): void
  {
    this.messagingClientService.initMessagingClientService();
    this.hideGlobalSpinnerLoading();
    this.router.navigate(['/devices/CFD/orders']);
  }

  hideGlobalSpinnerLoading(): void
  {
    document.getElementById('nb-global-spinner').style.display = 'none';
  }

  ngOnDestroy(): void
  {
    this.messagingClientService.disconnectMessagingClientService();
  }
}
